@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
@import url(https://fonts.googleapis.com/css?family=Overpass);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.container-center{
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #121212;
}

.login{
	background: #EAEAEC;
	width: 600px;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}

.login-area{
	display: flex;
	justify-content: center;
	background-color: #181c2e;
	width: 100%;
}

.login-area img{
	padding: 20px;
	width: 200px;
	height: 200px;
}

form{
	margin-top: 1.5em;
	width: 90%;
	display: flex;
	flex-direction: column;
}

form h1{
	text-align: center;
	margin-bottom: 0.5em;
	color: #181c2e;
}

.login input{
	margin-bottom: 15px;
	height: 35px;
	border: 0;
	border-radius: 7px;
	padding: 10px;
	background-color: #FFF;
}

form button{
	height: 35px;
	border: 0;
	border-radius: 7px;
	background-color: #181c2e;
	color: #FFF;
	font-size: 1.3em;
}

.login a{
	margin: 1.5em 0;
	color: #000;
	cursor: pointer;
}

.dashboard{
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.dashboard span{
	margin: 2em 0;
	font-weight: 600;
	font-size: 1.2em;
}

.new{
	width: 50%;
	margin: auto;
	margin-bottom: 1.5em;
	background-color: #0027a0;
	color: #fff;
	border: 0;
	padding: .3em;
	display: flex;
	justify-content: center;
	align-items: center;
	font-weight: 600;
	font-size: 1.1em;
	border-radius: 6px;
	transition: ease-in 0.2s;
}

.new:hover{
	background-color: #181c2e;
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
}

table{
	border: 1px solid #CCC;
	border-collapse: collapse;
	margin: 0;
	padding: 0;
	width: 100%;
	table-layout: fixed;
}

table caption{
	font-size: 1.5em;
	margin: .5em 0 .75em;
}

table tr{
	background-color: #f8f8f8;
	border: 1px solid #ddd;
	padding: .35em;
}

table th,
table td{
	padding: .62em;
	text-align: center;
}

table th{
	font-size: .85em;
	letter-spacing: .1em;
	text-transform: uppercase;
}

table td .action{
	border: 0;
	padding: 5px;
	margin-right: 2px;
	align-items: center;
	display: inline-block;
	border-radius: 4px;
}

table td .action svg{
	vertical-align: middle;
}

table td .badge{
	padding: 3px;
	border-radius: 3px;
	color: #FFF;
}

.fuck{
	background-color: blue;
	cursor: pointer;
}

@media screen and (max-width: 600px){
	table{
		border: 0;
	}

	table caption{
		font-size: 1.3em;
	}
	table thead{
		border: none;
		clip: rect(0 0 0 0 );
		height: 1px;
		margin: -1px;
		overflow: hidden;
		padding: 0;
		position: absolute;
		width: 1px;
	}

	table tr {
		border-bottom: 3px solid #ddd;
		display: block;
		margin-bottom: .65em;
	}

	table td{
		border-bottom: 1px solid #ddd;
		display: block;
		font-size: .8em;
		text-align: right;
	}

	table td::before{
		content: attr(data-label);
		float: left;
		font-weight: bold;
		text-transform: uppercase;
	}

	table td:last-child{
		border-bottom: 0;
	}
}

.sidebar{
	margin: 0;
	padding: 0;
	width: 200px;
	background-color: #181C2e;
	position: fixed;
	height: 100%;
	overflow: auto;
}

.sidebar div{
	background: url(/static/media/cover.42f5e4a3.jpg);
	background-color: #181C2e;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	height: 150px;
	padding-top: 30px;

}

.sidebar div img{
	border-radius: 90%;
	display: block;
	margin: auto;
	width: 90px;
	height: 90px;
	-webkit-filter: drop-shadow(2px 3px 6px #121212);
	filter: drop-shadow(2px 3px 6px #121212);
	object-fit:cover;
	border:3px black solid


}

.sidebar a{
	display: block;
	color: rgba(255,255,255, 0.7);
	padding: 16px;
	text-decoration: none;
	display: flex;
	flex-direction: row;
	align-items: center;
	transition: ease-in-out .4s;
  }

.sidebar a svg{
	margin-right: .5em;
}

.sidebar a:hover{
	background-color: #121212;
	color: #FFF;
}

.content{
	margin-left: 200px;
	padding: 1px 16px;
}

.welcome{
	display: flex;
	justify-content: center;
	color: #fff;
	font-weight: bold;
	margin-top: 5px;
	font-family: 'Architects Daughte', cursive;
	font-size: 14pt;

}

@media screen and (max-width: 700px){
	.sidebar{
		width: 100%;
		height: auto;
		position: relative;
	}

	div.content{
		margin-left: 0;
	}
	.sidebar a{
		float: left;
	}
	.sidebar div{
		display: none;
	}
	.sidebar a svg{
		display: none;
	}
	@media screen and (max-width: 400px){
		.sidebar a{
			text-align: center;
			float: none;
		}
		.sidebar a svg{
			display: none;
		}

	}

}

.title{
	display: flex;
	flex-direction: row;
	align-items: center;
	margin: 1em 0;
	border-radius: 5px;
	padding: .8em;
}

.title span{
	margin-left: 1em;
	font-size:  1.5em;
}

.container{
	display: flex;
	background-color: #6d7492;;
	border-radius: 5px;
	padding: .8em;
	align-items: center;
	margin-bottom: 1em;
}

.label-avatar{
	width: 280px;
	height: 280px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	cursor: pointer;
}

.label-avatar input{
	display: none;
}

.label-avatar span{
	z-index: 99;
	position: absolute;
	opacity: 0.7;
	transition: all .5s;
}


.label-avatar span:hover{
	opacity: 1;
	-webkit-transform: scale(1.4);
	        transform: scale(1.4);
}

.form-profile img{
	margin-bottom: 1em;
	border-radius: 50%;
	object-fit: cover;
}

.form-profile label{
	margin-bottom: .5em;
	font-size: 1.4em;
}

.form-profile input, text, select{
	margin-bottom: 1em;
	padding: .7em;
	border: 0;
	border-radius: 5px;
	max-width: 600px;
}

.form-profile input:disabled{
	cursor: not-allowed;
}

.form-profile button{
	max-width: 600px;
}

.logout-btn{
	padding: 8px 20px;
	background-color:transparent;
	border: 1px solid #121212;
	border-radius: 5px;
	font-size: 1.5em;
	display: flex;
	justify-content: center;
	align-items: center;
}
.save-btn{
	size: 10;
}

input, select, textarea{
	background-color: rgb(218, 235, 250);
}


.tags-input {
	display: flex;
	align-items: flex-start;
	flex-wrap: wrap;
	min-height: 48px;
	width: 480px;
	padding: 0 8px;
	border: 1px solid rgb(214, 216, 218);
	border-radius: 6px
}

#tags {
	display: flex;
	flex-wrap: wrap;
	padding: 0;
	margin: 8px 0 0 0;
}

.tag {
	width: auto;
	height: 32px;
	display: flex;
	align-items: center;
	justify-content: center;
	color: #fff;
	padding: 0 8px;
	font-size: 14px;
	list-style: none;
	border-radius: 6px;
	margin: 0 8px 8px 0;
	background: #0052cc;

	margin-top: 3px;
}
.tag-close-icon {
	display: block;
	width: 16px;
	height: 16px;
	line-height: 16px;
	text-align: center;
	font-size: 14px;
	margin-left: 8px;
	color: #0052cc;
	border-radius: 50%;
	background: #fff;
	cursor: pointer;
}

@media screen and (max-width: 567px) {
	.tags-input {
		width: calc(100vw - 32px);
	}
}

.select {
	margin-bottom: 1.5em;
}

.tag-container {
	min-width: 60%;
	max-width: 75%;
	display: flex;
	flex-wrap: wrap;
	min-height: 30px;
	border: 1px solid gray;
	border-radius: 5px;
	padding: 10px;
}
.tag {
	display: flex;
	align-items: center;
	padding: 5px;
	border: 1px solid gray;
	border-radius: 5px;
	height: 25px;
	margin: 2px 5px 2px 0px;
	color: white;
}

.tag-container input {
	border: none;
	flex: 1 1;
	outline: none;
	padding: 5;
}
.tag span {
	margin-left: 5px;
	cursor: pointer;
}

.react-tagsinput {
	background-color: #fff;
	border: 1px solid #ccc;
	overflow: hidden;
	padding-left: 5px;
	padding-top: 5px;
}

.react-tagsinput--focused {
	border-color: #181c2e;
}

.react-tagsinput-tag {
	background-color: #181c2e;
	border-radius: 2px;
	border: 1px solid #000;
	color: #fff;
	display: inline-block;
	font-family: sans-serif;
	font-size: 13px;
	font-weight: 400;
	margin-bottom: 5px;
	margin-right: 5px;
	padding: 5px;
}

.react-tagsinput-remove {
	cursor: pointer;
	font-weight: bold;
}

.react-tagsinput-tag a::before {
	content: ' ×';
}

.react-tagsinput-input {
	background: transparent;
	border: 0;
	color: rgb(187, 165, 165);
	font-family: sans-serif;
	font-size: 13px;
	font-weight: 400;
	margin-bottom: 6px;
	margin-top: 1px;
	outline: none;
	padding: 5px;
	width: 80px;
}

.foto-cidade {
	height: 100px;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	cursor: pointer;
}
.foto-cidade input {
	text-align: center;
	font-size: 12px;
	display: none;
}
.foto-cidade span {
	display: flex;
	justify-content: center;

	opacity: 0.7;
	transition: all 0.5s;
}

.btn-save-city{
	text-align: center;
	margin: auto;
	display: flex;
	width: 60%;
	height: 40;
	justify-content: center;
	align-items: center;
	background-color: #038c27;

}
.btn-save-city:hover{
	background-color: #07f746;
}
.city-name{
	text-align: center;
	font-size: 0.7em;
	position: relative;
}

form textarea {
	height: 105px;
	resize: none;
}

.Modal {
    position: absolute;
    top: 40px;
    left: 40px;
    right: 40px;
    bottom: 40px;
    background-color: papayawhip;
  }

  .Overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rebeccapurple;
  }




