	text-shadow: 0px -1px 0 #212121, 0px -1px 0 #212121, 0px 1px 0 #212121,
		0px 1px 0 #212121, -1px 0px 0 #212121, 1px 0px 0 #212121,
		-1px 0px 0 #212121, 1px 0px 0 #212121, -1px -1px 0 #212121,
		1px -1px 0 #212121, -1px 1px 0 #212121, 1px 1px 0 #212121,
		-1px 4px 0 #212121, 0px 4px 0 #212121, 1px 4px 0 #212121,
		0 8px 1px rgba(0, 0, 0, 0.1), 0 0 1px rgba(0, 0, 0, 0.1),
		0 1px 1px rgba(0, 0, 0, 0.3), 0 3px 1px rgba(0, 0, 0, 0.2),
		0 2px 2px rgba(0, 0, 0, 0.25), 0 5px 5px rgba(0, 0, 0, 0.2),
